// src/api/services/user/userService.ts

import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

interface User {
  id: number;
  email: string;
  name?: string;
  first_name?: string;
  last_name?: string;
  username?: string;
  role?: string;
  org_id?: string;
  phone_number?: string;
  role_id?: string;
  is_active?: boolean;
  is_super_admin?: boolean;
  created_at?: string;
  updated_at?: string;
  last_login?: string;
  role_ids?: string;
  role_names?: string;
  role_display_names?: string;
  roles?: Array<{
    id: number;
    name: string;
    display_name: string;
  }>;
}

interface GetCurrentUserResponse {
  success: boolean;
  message: string;
  data: User;
}

interface GetAllUsersResponse {
  success: boolean;
  message: string;
  data: User[];
}

export const getCurrentUserService = async (): Promise<User | null> => {
  try {
    const response = await axiosInstance.get<GetCurrentUserResponse>("/api/auth/me");

    if (response.status === 200 && response.data.success) {
      return response.data.data;
    }

    throw new Error("Failed to get current user");
  } catch (error: any) {
    console.error("Error getting current user:", error);
    if (error.response && error.response.data && error.response.data.message) {
      toast.error(error.response.data.message);
    } else {
      toast.error("Failed to get user information");
    }
    return null;
  }
};

export const updateUserProfileService = async (userData: Partial<User>): Promise<User | null> => {
  try {
    const response = await axiosInstance.put<GetCurrentUserResponse>(`/api/users/${userData.id}`, userData);

    if (response.status === 200) {
      toast.success("Profile updated successfully");
      return response.data.data;
    }

    throw new Error("Failed to update profile");
  } catch (error: any) {
    console.error("Error updating profile:", error);
    if (error.response && error.response.data && error.response.data.message) {
      toast.error(error.response.data.message);
    } else {
      toast.error("Failed to update profile");
    }
    return null;
  }
};

export const getAllUsersWithRoles = async (): Promise<GetAllUsersResponse> => {
  try {
    const response = await axiosInstance.get<GetAllUsersResponse>("/api/users/with-roles");

    if (response.status === 200) {
      return response.data;
    }

    throw new Error("Failed to get users");
  } catch (error: any) {
    console.error("Error getting users:", error);
    throw error;
  }
};

export const createUser = async (userData: {
  username: string;
  email: string;
  password: string;
  first_name: string;
  last_name: string;
  phone_number?: string;
  role_id: string;
  is_active?: boolean;
}): Promise<{success: boolean; message: string; data?: any}> => {
  try {
    const response = await axiosInstance.post("/api/users/create", userData);

    if (response.status === 201) {
      return response.data;
    }

    throw new Error("Failed to create user");
  } catch (error: any) {
    console.error("Error creating user:", error);
    throw error;
  }
};

export const updateUser = async (userId: number, userData: {
  username: string;
  email: string;
  first_name: string;
  last_name: string;
  phone_number?: string;
  role_id: string;
  is_active?: boolean;
}): Promise<{success: boolean; message: string; data?: any}> => {
  try {
    const response = await axiosInstance.put(`/api/users/${userId}`, userData);

    if (response.status === 200) {
      return { success: true, message: "User updated successfully" };
    }

    throw new Error("Failed to update user");
  } catch (error: any) {
    console.error("Error updating user:", error);
    throw error;
  }
};

export const deleteUser = async (userId: number): Promise<{success: boolean; message: string}> => {
  try {
    const response = await axiosInstance.delete(`/api/users/${userId}`);

    if (response.status === 200) {
      return response.data;
    }

    throw new Error("Failed to delete user");
  } catch (error: any) {
    console.error("Error deleting user:", error);
    throw error;
  }
};
